/* ============= Home ============= */
.home {
  display: flex;
}

.bloghome-container {
  display: flex;
  flex-direction: row;
}


/* ============= Blogheader ============ */
.blog_header {
  margin-top: 2px;
}

.blog_headerTitles {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Lora", serif;
  color: #444;
}

.blog_headerTitleSm {
  position: absolute;
  top: 18%;
  font-size: 20px;
}

.blog_headerTitleLg {
  position: absolute;
  top: 20%;
  font-size: 100px;
}

.blog_headerImg {
  width: 100%;
  height: 450px;
  margin-top: 2px;
  object-fit: cover;
}


/*=============== Posts ================  */
.blog_posts {
  flex: 9;
  display: flex;
  flex-wrap: wrap;
  margin: 20px;
}


/* =============== Post ================= */
.blog_post {
  width: 385px;
  margin: 0px 25px 40px 25px;
  display: flex;
  flex-direction: column;
}

.blog_postImg {
  width: 385px;
  height: 280px;
  object-fit: cover;
  border-radius: 7px;
}

.blog_postInfo {
  display: flex;
  flex-direction: column;
}

.blog_postCats {
  font-family: "Varela Round", Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 11px;
  color: #be9656;
  line-height: 19px;
  margin-top: 15px;
  margin-right: 10px;
  cursor: pointer;
}

.blog_postTitle {
  font-family: "Josefin Sans", Arial, Helvetica, sans-serif;
  font-size: 24px;
  font-weight: 900;
  margin-top: 15px;
  text-align: center;
  cursor: pointer;
}

.blog_postDate {
  font-family: "Lora", serif;
  font-style: italic;
  font-size: 13px;
  font-weight: 400;
  color: #999999;
  margin-top: 5px;
}

.blog_postDesc {
  font-family: "Varela Round", Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: #444444;
  margin-top: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

.blog_authorprofile {
  display: flex;
  flex-direction: row;

}

.blog_authorname {
  color: black;
  margin-left: 5px;
}

.blog_authorhour {
  display: flex;
  flex-direction: column;
  justify-content: right;
  align-items: flex-end;
  width: 100%;
  margin-right: 15px;
}

.blog_authorimgand {
  display: flex;
  flex-direction: row;
  width: 100%;
}


/* ================== Blog Sidebar =================== */
.blog_sidebar {
  justify-content: flex-end;
  width: 350px;
  flex: 3;
  height: fit-content;
  margin: 20px;
  padding-bottom: 30px;
  background-color: #fdfbfb;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.blog_sidebarItem {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.blog_sidebarTitle {
  margin: 10px;
  padding: 5px;
  width: 80%;
  border-top: solid 1px #a7a4a4;
  border-bottom: solid 1px #a7a4a4;
  text-align: center;
  font-family: "Varela Round", sans-serif;
  font-size: 12px;
  line-height: 19px;
  color: #222222;
  font-weight: 600;
}

.blog_sidebarItem>img {
  margin-top: 15px;
  width: 250px;
  height: 250px;
}

.blog_sidebarItem>p {
  padding: 30px;
}

.blog_sidebarList {
    list-style-type: none;
    margin-bottom: 30px;
  }

.sidebarListItem {
  display: inline-block;
  width: 50%;
  margin-top: 15px;
  cursor: pointer;
}

.blog_sidebarSocial {
  margin-top: 15px;
  width: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.blog_sidebarIcon {
  font-size: 16px;
  margin-left: 10px;
}

/* =============== BlogTopBar =============== */
.blog_top {
  width: 100%;
  height: 60px;
  background-color: rgba(17, 219, 207, 0.8);
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  z-index: 999;
  font-family: "Josefin Sans", sans-serif;
}
.blog_link{
  text-decoration: none;
}

.blog_topLeft {
  flex: 3;
  display: flex;
  align-items: flex-start;
  justify-content: left;
  margin-left: 20px;
}

.blog_topRight {
  flex: 3;
  display: flex;
  align-items: flex-end;
  justify-content: right;
  margin-right: 20px;
}

.blog_topIcon {
  font-size: 20px;
  margin-right: 10px;
  color: #444;
  cursor: pointer;
}

.blog_topCenter {
  flex: 6;
}

.blog_topList {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.blog_topListItem {
  margin-right: 20px;
  font-size: 18px;
  font-weight: 300;
  cursor: pointer;
}

.topListItem:hover {
  color: gray;
}

.blog_topImg {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 0px;
  cursor: pointer;
}

.topSearchIcon {
  font-size: 18px;
  color: #666;
  cursor: pointer;
}

/* =================== Write page =================*/
.write {
  padding-top: 0px;
}

.writeImg {
  margin-left: 150px;
  margin-top: 10px;
  width: 70vw;
  height: 250px;
  border-radius: 10px;
  object-fit: cover;
}

.writeForm {
  position: relative;
}

.writeFormGroup {
  margin-left: 150px;
  display: flex;
  align-items: center;
}

.writeIcon {
  width: 25px;
  height: 25px;
  font-size: 20px;
  border: 1px solid;
  border-radius: 50%;
  color: rgb(129, 125, 125);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.fileInput {
  margin-left: 150px;
}

.writeInput {
  font-size: 30px;
  border: none;
  padding: 20px;
  width: 70vw;
  height: 2vw;
  border: 1px solid black;
  border-radius: 5px;
}

.writeInput2 {
  font-size: 20px;
  border: none;
  padding: 20px;
  width: 70vw;
  height: 1.5vw;
  border: 1px solid black;
  border-radius: 5px;
}

.bloginput {
  margin-top: 10px;
}

.writeInput::placeholder {
  color: rgb(189, 185, 185);
  font-weight: 400;

}

.writeInput:focus {
  outline-style: none;
}

.blogtype {
  margin-top: 10px;
  border: 0.5px solid black;
  border-radius: 10px;
}

.writeText {
  width: 70vw;
  height: 25vh;
  font-family: inherit;
  font-size: 20px;
  margin-top: 10px;
}

.writeSubmit {
  position: absolute;
  top: 20px;
  right: 120px;
  color: white;
  background-color: teal;
  padding: 10px;
  border: none;
  border-radius: 10px;
  font-size: 16px;
  cursor: pointer;
  display: flex;
  align-items: center;
}


/* ================= Single Post ================== */
.singlePost {
  display: flex;
  /* width: 95%; */
  margin-left: 10px;
}

.singlePostWrapper {
  width: 700px;
  padding: 20px;
  padding-right: 0;
}

.singlePostImg {
  width: 100%;
  max-width: 700px;
  height: 300px;
  border-radius: 5px;
  object-fit: cover;
}

.singlePostTitle {
  /* text-align: center; */
  margin: 10px;
  font-size: 28px;
  font-family: "Lora", sans-serif;
}

.singlePostEdit {
  float: right;
  font-size: 16px;
}

.singlePostIcon {
  margin-left: 10px;
  cursor: pointer;
}

.singlePostIcon:first-child {
  color: teal;
}

.singlePostIcon:last-child {
  color: tomato;
}

.singlePostInfo {
  overflow-wrap: break-word;
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  color: #be9656;
  font-family: "Varela Round", Arial, Helvetica, sans-serif;
}

.singlePostAuthor {
  margin-left: 5px;
}

.singlePostDesc {
  color: #666;
  font-size: 18px;
  line-height: 25px;
}

.singlePostDesc::first-letter {
  margin-left: 20px;
  font-size: 30px;
  font-weight: 600;
}

.authorimg {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 15px;
  margin-left: 15px;
  cursor: pointer;
}

.comment_title {
  font-size: 20sp;
  text-align: center;
}

/* ============== Blog Detail ==================*/
.blog_single {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.blog_detailsingle {
  margin-left: 100px;
  max-width: 50%;
}

.blog_detailsidebar {
  display: flex;
  right: 0px;
  position: absolute;
  justify-content: right;
  align-items: flex-end;
}