/** SLIDESHOW **/
.slideshow,
.slideshow:after { 
    top: -16px; /*Not sure why I needed this fix*/
    width: 100%;
    height: 90vh;
    left: 0px;
    z-index: 0; 
}

.slideshow li span { 
		position: absolute;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    color: transparent;
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    opacity: 0;
    z-index: 0;
    -webkit-animation: imageAnimation 35s linear infinite 0s;
            animation: imageAnimation 35s linear infinite 0s; 
}



.slideshow li:nth-child(1) span { 
    background-image: url(/static/media/hero6.72f9fa16.jpg);
}
.slideshow li:nth-child(2) span { 
    background-image: url(/static/media/hero2.5c67e6d3.jpg);
    -webkit-animation-delay: 6s;
            animation-delay: 6s; 
}
.slideshow li:nth-child(3) span { 
    background-image: url(/static/media/hero3.d6aceb81.jpg);
    -webkit-animation-delay: 12s;
            animation-delay: 12s; 
}
.slideshow li:nth-child(4) span { 
    background-image: url(/static/media/hero4.7b895bea.jpg);
    -webkit-animation-delay: 18s;
            animation-delay: 18s; 
}
.slideshow li:nth-child(5) span { 
    background-image: url(/static/media/hero8.cafe885f.jpg);
    -webkit-animation-delay: 24s;
            animation-delay: 24s; 
}
.slideshow li:nth-child(6) span { 
  background-image: url(/static/media/hero1.1f6c73fc.jpg);
  -webkit-animation-delay: 30s;
          animation-delay: 30s; 
}
.slideshow li:nth-child(7) span { 
  background-image: url(/static/media/hero5.ddb9f363.jpg);
  -webkit-animation-delay: 36s;
          animation-delay: 36s; 
}
.slideshow li:nth-child(8) span { 
  background-image: url(/static/media/homepage.947c2ec0.jpg);
  -webkit-animation-delay: 36s;
          animation-delay: 36s; 
}



@-webkit-keyframes imageAnimation { 
    0% { opacity: 0; -webkit-animation-timing-function: ease-in; animation-timing-function: ease-in; }
    8% { opacity: 1; -webkit-animation-timing-function: ease-out; animation-timing-function: ease-out; }
    17% { opacity: 1 }
    25% { opacity: 0 }
    100% { opacity: 0 }
}



@keyframes imageAnimation { 
    0% { opacity: 0; -webkit-animation-timing-function: ease-in; animation-timing-function: ease-in; }
    8% { opacity: 1; -webkit-animation-timing-function: ease-out; animation-timing-function: ease-out; }
    17% { opacity: 1 }
    25% { opacity: 0 }
    100% { opacity: 0 }
}


@-webkit-keyframes titleAnimation { 
    0% { opacity: 0 }
    8% { opacity: 1 }
    17% { opacity: 1 }
    19% { opacity: 0 }
    100% { opacity: 0 }
}


@keyframes titleAnimation { 
    0% { opacity: 0 }
    8% { opacity: 1 }
    17% { opacity: 1 }
    19% { opacity: 0 }
    100% { opacity: 0 }
}


.no-cssanimations .cb-slideshow li span {
