  *::before,
  *::after { box-sizing: border-box; }
  
  
  
  .about-card {
    margin-right: 12%;
    position: relative;
    width: 700px;
    height: 450px;
    overflow: hidden;
    align-items: flex-end;
    justify-content: right;
    float: right;
    border-radius: 5px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
  }
  
  /*Light blue cover above the slide show*/
  .card::after {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    z-index: 900;
  
    display: block;
    width: 100%;
    height: 100%;
  
    background-color: rgba(140, 22, 115, 0.2);
  }
  
  .card_part {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 7;

    align-items: center;
    width: 700px;
    height: 450px;
    
    -webkit-transform: translateX( 500px );
    
            transform: translateX( 500px );
    background-image: url(/static/media/about7.bf24b3e6.jpg);
    background-size: 700px 450px;
    
    -webkit-animation: opaqTransition 28s cubic-bezier(0, 0, 0, 0.97) infinite;
    
            animation: opaqTransition 28s cubic-bezier(0, 0, 0, 0.97) infinite;
  }
  
  
  .card_part.card_part-two {
    z-index: 6;
    width: 100%;
    height: 100%;
    background-image: url(/static/media/about1.6a242ba9.jpg);
    -webkit-animation-delay: 7s;
            animation-delay: 7s;
  }
  
  .card_part.card_part-three {
    z-index: 5;
    background-image: url(/static/media/about2.6c6e122c.jpg);
    -webkit-animation-delay: 14s;
            animation-delay: 14s;
  }
  
  .card_part.card_part-four {
    z-index: 4;
    background-image: url(/static/media/about3.8b86b4fd.jpg);
    -webkit-animation-delay: 21s;
            animation-delay: 21s;
  }
  .card_part.card_part-five {
    z-index: 4;
    background-image: url(/static/media/about4.4d38a971.jpg);
    -webkit-animation-delay: 21s;
            animation-delay: 21s;
  }
  .card_part.card_part-six {
    z-index: 4;
    background-image: url(/static/media/about5.ddf9e178.jpg);
    -webkit-animation-delay: 21s;
            animation-delay: 21s;
  }
  
  
  @-webkit-keyframes opaqTransition {
    3% { -webkit-transform: translateX( 0 ); transform: translateX( 0 ); }
    25% { -webkit-transform: translateX( 0 ); transform: translateX( 0 ); }
    28% { -webkit-transform: translateX( -700px ); transform: translateX( -700px ); }
    100% { -webkit-transform: translateX( -700px ); transform: translateX( -700px ); }
  }
  
  
  @keyframes opaqTransition {
    3% { -webkit-transform: translateX( 0 ); transform: translateX( 0 ); }
    25% { -webkit-transform: translateX( 0 ); transform: translateX( 0 ); }
    28% { -webkit-transform: translateX( -700px ); transform: translateX( -700px ); }
    100% { -webkit-transform: translateX( -700px ); transform: translateX( -700px ); }
  }



