/* ============= Home ============= */
.home {
  display: flex;
}

.bloghome-container {
  display: flex;
  flex-direction: row;
}


/* ============= Blogheader ============ */
.blog_header {
  margin-top: 2px;
}

.blog_headerTitles {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Lora", serif;
  color: #444;
}

.blog_headerTitleSm {
  position: absolute;
  top: 18%;
  font-size: 20px;
}

.blog_headerTitleLg {
  position: absolute;
  top: 20%;
  font-size: 100px;
}

.blog_headerImg {
  width: 100%;
  height: 450px;
  margin-top: 2px;
  object-fit: cover;
}


/*=============== Posts ================  */
.blog_posts {
  flex: 9 1;
  display: flex;
  flex-wrap: wrap;
  margin: 20px;
}


/* =============== Post ================= */
.blog_post {
  width: 385px;
  margin: 0px 25px 40px 25px;
  display: flex;
  flex-direction: column;
}

.blog_postImg {
  width: 385px;
  height: 280px;
  object-fit: cover;
  border-radius: 7px;
}

.blog_postInfo {
  display: flex;
  flex-direction: column;
}

.blog_postCats {
  font-family: "Varela Round", Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 11px;
  color: #be9656;
  line-height: 19px;
  margin-top: 15px;
  margin-right: 10px;
  cursor: pointer;
}

.blog_postTitle {
  font-family: "Josefin Sans", Arial, Helvetica, sans-serif;
  font-size: 24px;
  font-weight: 900;
  margin-top: 15px;
  text-align: center;
  cursor: pointer;
}

.blog_postDate {
  font-family: "Lora", serif;
  font-style: italic;
  font-size: 13px;
  font-weight: 400;
  color: #999999;
  margin-top: 5px;
}

.blog_postDesc {
  font-family: "Varela Round", Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  color: #444444;
  margin-top: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

.blog_authorprofile {
  display: flex;
  flex-direction: row;

}

.blog_authorname {
  color: black;
  margin-left: 5px;
}

.blog_authorhour {
  display: flex;
  flex-direction: column;
  justify-content: right;
  align-items: flex-end;
  width: 100%;
  margin-right: 15px;
}

.blog_authorimgand {
  display: flex;
  flex-direction: row;
  width: 100%;
}


/* ================== Blog Sidebar =================== */
.blog_sidebar {
  justify-content: flex-end;
  width: 350px;
  flex: 3 1;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: 20px;
  padding-bottom: 30px;
  background-color: #fdfbfb;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.blog_sidebarItem {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.blog_sidebarTitle {
  margin: 10px;
  padding: 5px;
  width: 80%;
  border-top: solid 1px #a7a4a4;
  border-bottom: solid 1px #a7a4a4;
  text-align: center;
  font-family: "Varela Round", sans-serif;
  font-size: 12px;
  line-height: 19px;
  color: #222222;
  font-weight: 600;
}

.blog_sidebarItem>img {
  margin-top: 15px;
  width: 250px;
  height: 250px;
}

.blog_sidebarItem>p {
  padding: 30px;
}

.blog_sidebarList {
    list-style-type: none;
    margin-bottom: 30px;
  }

.sidebarListItem {
  display: inline-block;
  width: 50%;
  margin-top: 15px;
  cursor: pointer;
}

.blog_sidebarSocial {
  margin-top: 15px;
  width: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.blog_sidebarIcon {
  font-size: 16px;
  margin-left: 10px;
}

/* =============== BlogTopBar =============== */
.blog_top {
  width: 100%;
  height: 60px;
  background-color: rgba(17, 219, 207, 0.8);
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  z-index: 999;
  font-family: "Josefin Sans", sans-serif;
}
.blog_link{
  text-decoration: none;
}

.blog_topLeft {
  flex: 3 1;
  display: flex;
  align-items: flex-start;
  justify-content: left;
  margin-left: 20px;
}

.blog_topRight {
  flex: 3 1;
  display: flex;
  align-items: flex-end;
  justify-content: right;
  margin-right: 20px;
}

.blog_topIcon {
  font-size: 20px;
  margin-right: 10px;
  color: #444;
  cursor: pointer;
}

.blog_topCenter {
  flex: 6 1;
}

.blog_topList {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.blog_topListItem {
  margin-right: 20px;
  font-size: 18px;
  font-weight: 300;
  cursor: pointer;
}

.topListItem:hover {
  color: gray;
}

.blog_topImg {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 0px;
  cursor: pointer;
}

.topSearchIcon {
  font-size: 18px;
  color: #666;
  cursor: pointer;
}

